<template>
  <div>
    <headerBar :header-data="headerData" />
    <GoodsJykzBase :spxx="spxx" />
    <GoodsJykzXq :spxx="spxx" />
    <div class="common-space" />
    <Backtop />
  </div>
</template>

<script land="script" scoped>
import Backtop from '@/components/backtop/backtop.vue' // 返回顶部
import { getProductText } from '@/store/module.js'
import headerBar from '@/components/header/headerBar.vue'
import GoodsJykzBase from '@/components/layout/mall/goods/GoodsJykz/GoodsJykzBase'
import GoodsJykzXq from '@/components/layout/mall/goods/GoodsJykz/GoodsJykzXq'
// import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'OrderSnapshot',
  components: {
    Backtop,
    GoodsJykzBase,
    GoodsJykzXq,
    headerBar
  },
  // mixins: [pageRouter],
  data() {
    return {
      headerData: {
        title: '交易快照',
        leftUrl: 'back',
        leftText: ' '
      },
      detailId: '',
      spxx: {}
    }
  },
  created() {
    this.detailId = this.$store.state.goods.spid
    this.getJykzData()
  },
  methods: {
    getJykzData() {
      getProductText.data = { e_order_detail_id: this.detailId }
      this.asyncApiModule(getProductText).then(res => {
        this.spxx = res
      }).catch(() => {
      })
    }
  }
}
</script>

<style lang="css" scoped>
.scrollTop{bottom:50px;}
.wrapper {display: flex;align-items: center;justify-content: center;height: 100%;width: 100%;}
</style>
