<template>
  <div>
    <!-- 商品图片 begin -->
    <div id="section-1" class="goodsdetail-img" style="margin-top:65px">
      <div v-if="spxx && spxx.e_order_detail" class="slide-item">
        <img :src="spxx.e_order_detail.picture" :onerror="defaultImg" @click="imgPreview(spxx.e_order_detail.picture?spxx.e_order_detail.picture:defaultImg)">
      </div>
    </div>
    <!-- 商品图片 end -->

    <!-- 商品标题、商品信息 begin -->
    <div v-if="spxx && spxx.e_order_detail" class="detail-top">
      <div class="goods-price">
        <span class="num">{{ spxx.e_order_detail.price }}</span>元
        <del><span class="num">{{ spxx.e_order_detail.oringinal_price }}</span>元</del>
      </div>
      <div class="detail-title">{{ spxx.e_order_detail.product_name }}</div>
    </div>
    <div v-if="spxx && spxx.e_order_detail" class="goods-spe">
      <div class="content">
        <div class="common-main box">
          <div v-if="spxx.accessorList && spxx.accessorList.length>0" class="spe-list">
            <div class="spe-left"><span class="tit">配件</span><div class="info">
              <ul>
                <li v-for="(item,index) in spxx.accessorList" :key="index">
                  <span>{{ item.name }}</span>
                </li>
              </ul>
            </div>
            </div>
          </div>
          <div class="spe-list">
            <div class="spe-left"><span class="tit">购买数量</span><div class="info"><span class="info">{{ spxx.e_order_detail.total }}</span></div>
            </div>
          </div>
          <div v-if="spxx && spxx.e_order_detail.dsbs==='KJXY'" class="spe-list">
            <div class="spe-left"><span class="tit">选择供应商</span><span class="info">{{ spxx.e_order.gysmc }}</span></div>
          </div>
          <div class="spe-list">
            <div class="spe-left"><span class="tit">送至</span><span class="info">{{ spxx.area }}</span></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import { ImagePreview } from 'vant'
export default {
  name: 'GoodsJykzBase',
  props: {
    spxx: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"'
    }
  },
  created() {
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    }
  }
}
</script>

<style lang="css" scoped>
.scrollTop{bottom:50px;}
.wrapper {display: flex;align-items: center;justify-content: center;height: 100%;width: 100%;}
</style>
